import {Injectable} from '@angular/core';
import {ToggleSubjects} from '../constants/toggle-subjects';
import {BehaviorSubject, of} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ToggleService {
  protected toggles$: Map<string, BehaviorSubject<boolean>> = new Map();

  constructor() {
    this.init(ToggleSubjects.NAVBAR);
    this.init(ToggleSubjects.LOADER);
  }

  get(subject: string) {
    return this.toggles$.has(subject)
      ? this.toggles$.get(subject).asObservable()
      : this.init(subject).asObservable();
  }

  init(subjectKey: string) {
    const subject: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);
    this.toggles$.set(subjectKey, subject);
    return subject;
  }

  dispatch(subject: string, status: boolean) {
    if (this.toggles$.has(subject)) {
      this.toggles$.get(subject).next(status);
    }

  }

}
