import {Component, OnDestroy, OnInit} from '@angular/core';
import {ToggleSubjects} from "../../../constants/toggle-subjects";
import {ToggleService} from "../../../services/toggle.service";
import {INavigationOption} from "../../interfaces/navigation-option.interface";
import {NavigationOptions} from "../../constants/navigation-options.contant";

@Component({
  selector: 'sug-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit, OnDestroy {
  public options: Array<INavigationOption> = NavigationOptions;
  public snippets: Array<{ icon: string, description: string, title: string }> = [
    {
      icon: '',
      title: 'Enhanch your structure',
      description: 'Sugoi gives you ORM with lifecycle, singleton services and decorators support'
    },
    {
      icon: '',
      title: 'Optimized development',
      description: 'By using Sugoi you can focus your business logic and optimize your development'
    },
    {
      icon: '',
      title: 'Small',
      description: 'Each Sugoi module is by the size of 800KB'
    }
  ];

  constructor(private _toggle: ToggleService) {
  }

  ngOnInit() {
    this._toggle.dispatch(ToggleSubjects.NAVBAR, false);
  }

  ngOnDestroy(): void {
    this._toggle.dispatch(ToggleSubjects.NAVBAR, true);
  }

}
